<template>
    <div class="auth">
        <nav-bar
            title="提现认证"
            :hairline="false" />
        <div class="auth__form">
            <div :class="[ 'auth__form-group', { error }]">
                <div
                    class="auth__form-item"
                    v-if="identityInfo.needName">
                    <field
                        label="真实姓名"
                        placeholder="请输入微信实名认证的姓名"
                        v-model="identityInfo.realName"
                        @focus="inputFocusHandler" />
                    <div
                        v-show="identityInfo.realName"
                        class="auth__tip exclamation-mark">
                        <span>需与微信实名认证的姓名相同，如不一致将打款失败</span>
                    </div>
                </div>
                <div
                    v-if="identityInfo.needCardNo"
                    class="auth__form-item">
                    <field
                        label="身份证号"
                        placeholder="请输入正确的身份证号"
                        v-model="identityInfo.cardNo"
                        @focus="inputFocusHandler" />
                    <div
                        class="auth__tip question-mark"
                        @click="highlightTip(0)">
                        <span class="underline">为什么要填身份证号</span>
                    </div>
                </div>
            </div>
            <field
                label="微信授权"
                :input="false">
                <div
                    v-if="identityInfo.isWechatAuth"
                    class="auth__wechat"
                    slot="text">
                    <template v-if="identityInfo.wechatNickname">
                        "<span class="auth__wechatNickname">
                            {{ identityInfo.wechatNickname }}
                        </span>"
                    </template>
                    <span>已授权</span>
                </div>
                <div
                    v-else
                    slot="button"
                    class="auth__btn arrow-right"
                    @click="wechatAuthHandler">去授权</div>
            </field>
            <field
                label="绑定手机号"
                :input="false"
                :disabled="!!identityInfo.phoneNumber">
                <div
                    v-if="identityInfo.phoneNumber"
                    slot="text">"{{ identityInfo.phoneNumber }}"已绑定</div>
                <div
                    v-else
                    slot="button"
                    class="auth__btn arrow-right"
                    @click="phoneAuthHandler">去绑定</div>
            </field>
        </div>
        <div class="auth__footer">
            <Button
                :disabled="!canAuth"
                :customStyle="{ width: '100%', height: '0.47rem', fontSize: '0.16rem' }"
                @click="authHandler">立即认证</Button>
            <rule
                class="auth__rule"
                :need-card-no="identityInfo.needCardNo"
                :highlighted-tip-index="highlightedTipIndex" />
        </div>
    </div>
</template>
<script>
import debounce from 'lodash.debounce';
import config from '@/config';
import NavBar from '@/components/NavBar';
import Field from '@/components/Field';
import Button from '@/components/Button';
import { get, post } from '@/api/withdrawAuth';
import {
    toast,
    bindWx,
    bindPhoneDetail,
    getBindWxNickName } from '@/utils/jsApiV2/client';
import * as event from '@/utils/event';
import Rule from './Rule';

export default {
    name: 'Withdraw',
    components: {
        NavBar,
        Field,
        Button,
        Rule,
    },
    data() {
        return {
            identityInfo: {
                realName: null,
                cardNo: null,
                needCardNo: false,
            },
            error: false,
            highlightedTipIndex: null,
        };
    },
    computed: {
        canAuth() {
            return (!this.identityInfo.needName
                || (this.identityInfo.needName && this.identityInfo.realName))
            && (!this.identityInfo.needCardNo
                || (this.identityInfo.needCardNo && this.identityInfo.cardNo))
            && this.identityInfo.isWechatAuth && this.identityInfo.phoneNumber
            && !this.identityInfo.auth;
        },
    },
    beforeRouteEnter(to, from, next) {
        next(vm => vm.getIdentityInfo());
    },
    created() {
        event.addCustomEventListener('focus', this.getIdentityInfo);

        this.$o2o({ e_c: '提现', e_a: '曝光', e_n: '提现认证' });
    },
    methods: {
        getIdentityInfo() {
            get({ forceAuth: this.$route.query.forceAuth === '1' })
                .then((data) => {
                    if (!data.isWechatAuth) {
                        data.isWechatAuth = !!getBindWxNickName(); // eslint-disable-line
                        data.wechatNickname = getBindWxNickName(); // eslint-disable-line
                    }

                    if (this.identityInfo.realName) {
                        data.realName = this.identityInfo.realName; // eslint-disable-line
                    }

                    if (this.identityInfo.cardNo) {
                        data.cardNo = this.identityInfo.cardNo; // eslint-disable-line
                    }

                    this.identityInfo = {
                        ...this.identityInfo,
                        ...data,
                    };

                    if (this.identityInfo.auth) {
                        this.goAuthResult();
                    }

                    this.$root.hidePlaceholder();
                }).catch(() => {
                    this.$error(this.$route.name);
                });
        },
        highlightTip(index) {
            this.highlightedTipIndex = index;
        },
        wechatAuthHandler: debounce(function handler() {
            if (this.identityInfo.isWechatAuth) return;

            bindWx();

            this.$o2o({ e_c: '提现', e_a: '点击', e_n: '去授权' });
        }, config.debounceWait),
        phoneAuthHandler: debounce(function handler() {
            if (this.identityInfo.phoneNumber) return;

            bindPhoneDetail('withdrawAuth');

            this.$o2o({ e_c: '提现', e_a: '点击', e_n: '去绑定' });
        }, config.debounceWait),
        goAuthResult() {
            this.$router.replace({
                name: 'withdrawAuthResult',
                query: {
                    cardNo: this.identityInfo.needCardNo ? 1 : 0,
                    has5Yuan: this.$route.query.has5Yuan,
                },
            });
        },
        authHandler: debounce(function handler() {
            if (this.identityInfo.needName && !this.identityInfo.realName.trim()) {
                return toast('请输入微信实名认证的姓名');
            }

            return post({
                realName: this.identityInfo.realName,
                cardNo: this.identityInfo.cardNo || '',
                forceAuth: this.$route.query.forceAuth === '1',
            })
                .then(() => {
                    this.goAuthResult();

                    this.$o2o({
                        e_c: '提现',
                        e_a: '点击',
                        e_n: '立刻认证',
                        e_x: { result: '认证成功' },
                    });
                })
                .catch((res) => {
                    this.error = true;

                    toast(res.msg || '网络错误');

                    this.$o2o({
                        e_c: '提现',
                        e_a: '点击',
                        e_n: '立刻认证',
                        e_x: { result: '认证失败' },
                    });
                });
        }, config.debounceWait),
        inputFocusHandler() {
            this.error = false;
        },
    },
};
</script>
<style scoped>
.auth {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.auth .nav-bar {
    flex-shrink: 0;
}

.auth__form-group.error .auth__form-item >>> .field__input {
    color: #f00;
}

.auth__form-item:last-child .field::after,
.auth__form .field:last-child::after {
    display: none;
}

.auth__form-item:only-child {
    border-bottom: 10px solid #f7f7f7;
}

.auth__tip {
    padding: 5px 0 5px 20px;
    font-size: 13px;
    line-height: 17px;
    color: #3577e5;
    background-color: #f3f3f3;
}

.auth__tip::before {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 16px;
    height: 16px;
    margin-right: 3px;
    color: #3577e5;
    border: 1px solid #3577e5;
    border-radius: 999px;
}

.exclamation-mark::before {
    content: "!";
}

.question-mark::before {
    content: "?";
}

.underline {
    text-decoration: underline;
}

.auth__tip.underline::before {
    text-decoration: none;
}

.auth__wechat,
.auth__account {
    display: flex;
}

.auth__wechatNickname {
    max-width: 150px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.auth__form .success {
    color: #000;
}

.auth__btn {
    font-size: 13px;
    color: #3577e5;
}

.auth__footer {
    flex: 1;
    padding: 14px 15px 0 15px;
    background-color: #f7f7f7;
}

.auth__rule {
    margin: 27px 5px 48px 5px;
}

.arrow-right::after {
    background-image: url(./image/arrow-right-bule.png);
}
</style>

