<template>
    <div class="field Hairline Hairline_bottom">
        <label class="field__label">
            <slot name="label">{{ label }}</slot>
            <input
                class="field__input"
                v-if="input"
                :value="value"
                :type="type"
                :placeholder="placeholder"
                :disabled="disabled"
                @focus="$emit('focus')"
                @input="$emit('input', $event.target.value)">
        </label>
        <div class="field__text">
            <slot name="button" />
            <slot name="text" />
        </div>
    </div>
</template>
<script>
export default {
    name: 'Field',
    props: {
        label: {
            type: String,
            default: null,
        },
        value: {
            type: [String, Number],
            default: null,
        },
        placeholder: {
            type: String,
            default: '',
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        type: {
            type: String,
            default: 'text',
        },
        input: {
            type: Boolean,
            default: true,
        },
    },
};
</script>
