<template>
    <div class="rule">
        <h1>{{ rules.title }}</h1>
        <p>
            <ul>
                <li
                    v-for="(rule, index) in finalRules"
                    :key="rule"
                    :class="{ highlight: index === highlightedTipIndex }">{{ rule }}</li>
            </ul>
        </p>
    </div>
</template>

<script>
import { getConfigByKey } from '@/api/common';
import { apolloWithdrawAuthRuleKey } from './config';

export default {
    name: 'Rule',
    props: {
        highlightedTipIndex: {
            type: Number,
            default: null,
        },
        needCardNo: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {
            rules: {
                content: [],
            },
        };
    },
    computed: {
        finalRules() {
            if (!this.needCardNo) {
                const list = [];
                this.rules.content.forEach((str, index) => {
                    if (index > 0) {
                        list.push(index + str.slice(1));
                    }
                });
                return list;
            }
            return this.rules.content;
        },
    },
    created() {
        this.getRules();
    },
    methods: {
        getRules() {
            getConfigByKey({
                key: apolloWithdrawAuthRuleKey,
            })
                .then((res) => {
                    this.rules = res.data;
                })
                .catch(() => {
                    this.$error(this.$route.name);
                });
        },
    },
};
</script>

<style scoped>
.rule {
    font-size: 12px;
    line-height: 20px;
    color: rgba(0, 0, 0, 0.3);
}

.rule h1 {
    margin: 10px 0;
    font-size: 15px;
    color: rgba(51, 51, 51, 0.5);
}

.rule span {
    text-decoration: underline;
    color: #3ba7f7;
}

.highlight {
    font-weight: 600;
    color: #3577e5;
}
</style>
